// tests/unit/screens/EmotionLogScreen.unit.test.tsx
import React from 'react';
import { render, fireEvent, waitFor } from '@testing-library/react-native';
import EmotionLogScreen from '../../../src/screens/EmotionLogScreen';
import emotionService from '../../../src/services/api/emotionService';

// Alert 모킹
jest.mock('react-native/Libraries/Alert/Alert', () => ({
  alert: jest.fn(),
}));

// DevSettings 모킹 방식 수정
jest.mock('react-native/Libraries/Utilities/DevSettings', () => ({
  addMenuItem: jest.fn(),
}));

// emotionService 모킹
jest.mock('../../../src/services/api/emotionService', () => ({
  getAllEmotions: jest.fn(),
  recordEmotions: jest.fn(),
}));

describe('EmotionLogScreen', () => {
  const mockNavigation = {
    goBack: jest.fn(),
  };

  const mockEmotions = [
    { 
      emotion_id: 1, 
      name: '행복', 
      icon: 'happy-icon', 
      color: '#FFD700' 
    },
    { 
      emotion_id: 2, 
      name: '슬픔', 
      icon: 'sad-icon', 
      color: '#4682B4' 
    }
  ];

  beforeEach(() => {
    jest.clearAllMocks();
    
    // 모든 테스트에 대한 기본 모킹 설정
    (emotionService.getAllEmotions as jest.Mock).mockResolvedValue({
      data: { 
        status: 'success', 
        data: mockEmotions 
      }
    });
  });

  it('renders emotion chips correctly', async () => {
    const { getByText } = render(
      <EmotionLogScreen navigation={mockNavigation} />
    );

    // 감정 데이터 로딩 대기
    await waitFor(() => {
      expect(getByText('행복')).toBeTruthy();
      expect(getByText('슬픔')).toBeTruthy();
    });
  });

  it('allows selecting and deselecting emotions', async () => {
    const { getByText, getByTestId } = render(
      <EmotionLogScreen navigation={mockNavigation} />
    );

    // 감정 데이터 로딩 대기
    await waitFor(() => {
      const happyChip = getByText('행복');
      
      // 감정 선택
      fireEvent.press(happyChip);
      
      // 제출 버튼 확인
      const submitButton = getByTestId('emotion-submit-button');
      expect(submitButton.props.accessibilityState.disabled).toBeFalsy();
    });
  });

  it('shows error when no emotion is selected', async () => {
    const { getByTestId } = render(
      <EmotionLogScreen navigation={mockNavigation} />
    );

    await waitFor(() => {
      const submitButton = getByTestId('emotion-submit-button');
      
      // 제출 버튼 비활성화 확인
      expect(submitButton.props.accessibilityState.disabled).toBeTruthy();
    });
  });

  it('submits emotions successfully', async () => {
    // recordEmotions 성공 모킹
    (emotionService.recordEmotions as jest.Mock).mockResolvedValue({
      data: { status: 'success' }
    });

    const { getByText, getByTestId } = render(
      <EmotionLogScreen navigation={mockNavigation} />
    );

    await waitFor(() => {
      // 감정 선택
      const happyChip = getByText('행복');
      fireEvent.press(happyChip);

      // 메모 입력
      const noteInput = getByTestId('emotion-note-input');
      fireEvent.changeText(noteInput, '오늘은 매우 행복한 날');

      // 제출 버튼 클릭
      const submitButton = getByTestId('emotion-submit-button');
      fireEvent.press(submitButton);
    });

    // 제출 후 검증
    await waitFor(() => {
      expect(emotionService.recordEmotions).toHaveBeenCalledWith({
        emotion_ids: [1],
        note: '오늘은 매우 행복한 날'
      });
    });
  });

  it('handles emotion submission error', async () => {
    // recordEmotions 실패 모킹
    (emotionService.recordEmotions as jest.Mock).mockRejectedValue({
      response: {
        data: {
          message: '감정 기록 중 오류가 발생했습니다.'
        }
      }
    });

    const { getByText, getByTestId } = render(
      <EmotionLogScreen navigation={mockNavigation} />
    );

    await waitFor(() => {
      // 감정 선택
      const happyChip = getByText('행복');
      fireEvent.press(happyChip);

      // 제출 버튼 클릭
      const submitButton = getByTestId('emotion-submit-button');
      fireEvent.press(submitButton);
    });

    // 오류 얼럿 호출 확인
    await waitFor(() => {
      expect(require('react-native/Libraries/Alert/Alert').alert).toHaveBeenCalledWith(
        '오류',
        '감정 기록 중 오류가 발생했습니다.'
      );
    });
  });
});